import { useContext, useMemo, useState } from "react";
import FormUtil from "../../../utils/form/formUtiil";
import FuseList from "./fuseList";
import { GlobalContext } from "../../../contents/entry/systemEntry";
import StoryUtil from "../../../contents/create/launcher/util/storyUtil";
import FuseMainteDialog from "./fuseMainteDialog";
import { FuseBean } from "../../../contents/create/launcher/storyTab";
import LogicUtil from "../../../contents/create/factory/util/logicUtil";
import LogicElementUtil from "../../../contents/create/factory/util/logicElementUtil";
import BuildUtil from "../../../contents/create/preview/buildUtil";

const SPEditStoryFrame = () => {
    const { store, setStore, dispatcher } = useContext(GlobalContext);

    const [focus, setFocus] = useState<number>(-1);
    const [isPreview, setPreview] = useState<boolean>(false);

    const storyData = store.storyData as StoryUtil.StoryData;

    const fuseList = storyData.fuseList;
    const limitFrame = storyData.limitFrame;
    const limitParticle = storyData.limitParticle;

    const seedList: FuseBean[] = useMemo(() => {
        if (store.libraryData == null) return [];
        const root = store.libraryData.data as LogicUtil.NodeRoot;
        const seedWrapList = (root.seedMng.data as LogicUtil.NodeSeedManager).defs;
        return seedWrapList.map(wrap => {
            const seed = wrap.data as LogicUtil.NodeSeedItem;

            // 
            const argMng = LogicElementUtil.getInnerWrapFixed(wrap, 'interface-mng', 'seedarg-mng').data as LogicUtil.NodeSeedArgManager;
            const args = argMng.args.map(wrap => wrap.data as LogicUtil.NodeSeedArgItem);

            // 
            const groupMng = LogicElementUtil.getInnerWrap(wrap, 'interface-mng', 'formgrp-mng');
            let formgrps: LogicUtil.NodeFromGrpItem[] = [];
            if (groupMng != null) {
                formgrps = (groupMng.data as LogicUtil.NodeFromgrpManager).defines.map(wrap => wrap.data as LogicUtil.NodeFromGrpItem);
            }

            // 
            const sampleMng = LogicElementUtil.getInnerWrap(wrap, 'sample-mng')?.data as LogicUtil.NodeSampleManager;
            const presets = sampleMng.samples.map(wrap => wrap.data as LogicUtil.NodeSampleItem);

            return {
                name: seed.name,
                disp: seed.disp,
                args, formgrps, presets
            }
        });
    }, [store.libraryData]);

    return (<>
        <FormUtil.ButtonRecord
            align="left"
            buttons={[
                {
                    label: 'Preview all',
                    isEnable: fuseList.length > 0,
                    callback: () => {
                        fuseList.sort((a, b) => {
                            return a.frame - b.frame;
                        });
                        storyData.fuseList = fuseList.slice();
                        // dispatcher.updateStore();
                        setPreview(true);
                    }
                }
            ]}
        />
        <FormUtil.BorderPanel
            title="fuse"
            height={686}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    align="left"
                    buttons={[
                        {
                            label: 'Add',
                            callback: () => {
                                store.system.dialog = <FuseMainteDialog
                                    focus={-1}
                                    seedList={seedList}
                                    cancelAction={() => {
                                    }}
                                    modifyAction={(frame: number, seedId: string, values: number[]) => {
                                    }}
                                />;
                                setStore({ ...store });
                            }
                        },
                        {
                            label: 'Align',
                            callback: () => {
                                fuseList.sort((a, b) => {
                                    return a.frame - b.frame;
                                });
                                storyData.fuseList = fuseList.slice();
                                setStore({ ...store });
                            }
                        }
                    ]}
                />
                <FuseList
                    focus={focus}
                    setFocus={setFocus}
                    seedList={seedList}
                />
            </>}
        />
        {!isPreview ? <></> : <BuildUtil.BuildFrame closeAction={() => {
            setPreview(false);
        }} />}
    </>);
}

export default SPEditStoryFrame;